// vmvuethisvuextoken
const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: 'http://192.168.204.128:5000', // 
		// method: 'POST',            
		// jsonJSON.parse()
		dataType: 'json',
		showLoading: true, // loading
		loadingText: '...', // loading
		loadingTime: 800, // ms
		originalData: true, // 
		loadingMask: true, // loading
		// 
		header: {
			'content-type': 'application/json;charset=UTF-8'
		},
	});

	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// token
		// vuextokenuViewvuex
		// https://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;

		config.header.Authorization = 'bearer '+ vm.vuex_token         //bearer

		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;

		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;

		// tokenStorage
		// Storage
		// const token = uni.getStorageSync('token');
		// config.header.token = token;
		// config.header.Token = 'xxxxxx';

		// urlurlthis.$u.get(url)urlurltokennoToken
		if (config.url == '/user/login') config.header.noToken = true;
		// configreturn
		return config;
		// returnfalse
		// if(config.url == '/user/rest') return false; // 
	}

	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		// console.log(res);      //
		// return res;          //
		const {
			statusCode,
			data
		} = res //resstatusCodedatastatusres
		// console.log(statusCode);
		if (statusCode < 400) {
			// rescoderesult
			// res.resultthis.$u.post(url).then(res => {})thenres
			// originalDatatrue
			return data;
		} else if (statusCode == 400) { //Authorization401
			// 401token
			vm.$u.toast(data.message)
			setTimeout(() => {
				// uView
				vm.$u.route('/pages/user/login')
			}, 1500)
			return false;
		} else if (statusCode == 401) { 
			//Authorization401
			//401;token
			if(data.message=="Unauthorized"){
				vm.$u.toast('');
			}else{
				// 401token
				vm.$u.toast('');
				setTimeout(() => {
					// uView
					vm.$u.route('/pages/user/login')
				}, 1500)
				return false;
			}
		} else if (statusCode == 422) { //422
			const {
				errors
			} = data //errorsdata
			// console.log(errors);          //toast
			console.log(Object.values(errors)); //valuesvalues
			vm.$u.toast(Object.values(errors)[0][0]);
			return false;
		} else if(statusCode==500) {
			// falsePromisereject
			// this.$u.post(url).then().catch(res=>{})catchres
			console.log("500");
			return false;
		}
		else{
			return false
		}
	}


	// ====vmpatch
	// header
	vm.$u.patch = (url, params = {}, header = {}) => {
		//POSTpatch
		const _params = {
			...params, //params_method
			_method: 'PATCH'
		}
		return vm.$u.post(url, _params, header)
	}
}

export default {
	install
}
