//apivue
const install = (Vue, vm) => {
	vm.$u.utils = {}
	// 1
	vm.$u.utils.isLogin = async () => {
		// tokenFALSEonload
		// const token = vm.vuex_token
		const res = await vm.$u.api.jwt_confirm() //(20token)
		if (res.code==507) {
			//  
			const currentPage = getCurrentPages().pop() //
			
			// ==========================url
			const {options, route} = currentPage        //currentPage
								//optionsroute
			
			// key
			const optionsKeys = Object.keys(options) // ['id','name']
			
			// params     eg.  ?id=3&name='tom'
			let params = ''
			if (optionsKeys.length !== 0) {
				params = optionsKeys.reduce((pre, current) => {
					return pre + current + '=' + options[current] + '&'
				}, '?').slice(0, -1)
			}
			
			// ==========================vuex
			uni.setStorageSync("back_url", route + params) //sync

			vm.$u.toast('')
			setTimeout(() => {
				vm.$u.route({
					type: 'redirect',
					url: 'pages/auth/login',
				})
			}, 1500)
			return false
		}
		return true
	}
	
	// 2
	vm.$u.utils.updateUser =async () => {
		// vuexvuex_user
		const userInfo = await vm.$u.api.userInfo()
		vm.$u.vuex("vuex_user", userInfo)
	}
}


export default {
	install
}
