const install = (Vue, vm) => {
	// params
	// vm.$u.api(vmthisthis.$u.api)

	vm.$u.api = {} // api
	// indexindexUrl
	vm.$u.api.index = (params = {}) => vm.$u.get('/api/index', params); //{ return xxx }promise
	
	// 
	vm.$u.api.depart = (params = {}) => vm.$u.get('/depart', params);
	
	// url
	vm.$u.api.authLogin = (params) => vm.$u.post('/user/jwtlog', params); //
	vm.$u.api.authRegister = (params) => vm.$u.post('/user/reg', params) //
	vm.$u.api.jwt_confirm = ()=> vm.$u.get('/user/jwt_confirm')  //jwt
	
	// API
	vm.$u.api.userInfo = () => vm.$u.get('/user/auth_info'); //
	vm.$u.api.userInfoUpdate = (params) => vm.$u.put('/user/auth_update', params) //
}

export default {
	install
}
